import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import './assets/scss/style.scss';
import Vuelidate from 'vuelidate';

Vue.use(Vuelidate);
Vue.config.productionTip = false;

Vue.directive('phone', {
  bind(el) {
    function replaceNumberForInput(value) {
      let val = '';
      const x = value
        .replace(/\D/g, '')
        .match(/(\d{0,1})(\d{0,3})(\d{0,3})(\d{0,4})/);

      if (!x[2] && x[1] !== '') {
        val = x[1] === '8' ? x[1] : '8' + x[1];
      } else {
        val = !x[3]
          ? x[1] + x[2]
          : x[1] + '(' + x[2] + ')' + x[3] + (x[4] ? '-' + x[4] : '');
      }

      return val;
    }

    function replaceNumberForPaste(value) {
      const r = value.replace(/\D/g, '');
      let val = r;
      if (val.charAt(0) === '8') {
        val = '8' + val.slice(1);
      }
      return replaceNumberForInput(val);
    }

    el.oninput = function (e) {
      if (!e.isTrusted) {
        return;
      }
      this.value = replaceNumberForInput(this.value);
    };

    el.onpaste = function () {
      setTimeout(() => {
        const pasteVal = el.value;
        this.value = replaceNumberForPaste(pasteVal);
      });
    };
  },
});

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');

