<template>
  <div class="registr">
    <div class="holder" v-if="isLoading">
      <div class="preloader">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="container">
      <div class="registr_title">
        <h1>Регистрация пользователя</h1>
      </div>
      <div class="">
        <form class="registr-form">
          <div class="registr-form__row">
            <ul id="progressbar">
              <li class="active" @click="switchTabsClass('contact')">
                Регистрация пользователя
              </li>
              <li
                id="choice-weapons"
                :class="{ notactive: this.currentUser === null }"
                @click="switchTabsClass('block')"
              >
                <!-- :class="{ notactive: this.currentUser === null }" -->
                Ввод данных об оружии
              </li>
              <li
                id="active-block"
                :class="{
                  notactive: this.getWeaponItem === null,
                }"
                @click="switchTabsClass('post')"
              >
                Активация блока
              </li>
              <!-- this.getWeaponItem.length === 0 || -->
            </ul>
            <div class="registr-form__column">
              <div
                class="registr-form__wrap"
                data-tab="contact"
                v-if="isBlockActive"
              >
                <ContactInfo @validFirstBlock="changeFirstBlock" />
              </div>
              <div
                class="registr-form__wrap"
                data-tab="block"
                id="titletwo"
                v-if="isPostActive"
              >
                <ChoiceOfWeapon @changeSecondBlock="changeSecondBlock" />
              </div>
              <!-- data-tab="post" -->
              <div class="order-wrap2" data-tab="pay" id="titlethee">
                <div class="order-pay" v-if="isPayActive">
                  <ActiveBlock />
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import ContactInfo from '@/components/ContactInfo.vue';
import ChoiceOfWeapon from '@/components/ChoiceOfWeapon.vue';
import ActiveBlock from '@/components/ActiveBlock.vue';

import axios from 'axios';
import { mapState } from 'vuex';
export default {
  data() {
    return {
      title: 'Регистрация',
      //Блок контактной информации
      isBlockActive: true,
      isPostActive: false,
      isPayActive: false,

      isButtonDisabled: false,
    };
  },
  components: {
    ContactInfo,
    ChoiceOfWeapon,
    ActiveBlock,
  },

  computed: {
    ...mapState({
      isAuthenticated: (state) => state.auth.isAuthenticated,
      token: (state) => state.auth.token,
      currentUser: (state) => state.register.currentUser,
      isLoading: (state) => state.clients.isLoading,
      getWeaponItem: (state) => state.register.getWeaponItem,
    }),
  },

  methods: {
    //смена первого блока
    changeFirstBlock() {
      this.switchTabsClass('block');
    },
    //смена второго блока
    changeSecondBlock() {
      this.switchTabsClass('post');
    },
    switchTabsClass(tab) {
      if (tab === 'block') {
        if (this.currentUser != null) {
          this.isBlockActive = false;
          this.isPostActive = true;
          this.isPayActive = false;
          let elem = document.getElementById('choice-weapons');
          elem.classList.add('active');
        }
      } else if (tab === 'post') {
        if (this.getWeaponItem != null) {
          this.isBlockActive = false;
          this.isPostActive = false;
          this.isPayActive = true;
          let elemSecond = document.getElementById('choice-weapons');
          elemSecond.classList.add('active');
          let elemThird = document.getElementById('active-block');
          elemThird.classList.add('active');
        }
      } else {
        this.isBlockActive = true;
        this.isPostActive = false;
        this.isPayActive = false;
      }
    },
  },
  mounted() {
    if (this.token === 'undefined' || this.token === '') {
      this.$router.push({ name: 'login' });
    }
  },
  beforeMount() {
    document.title = this.title;
  },
  beforeUpdate() {
    document.title = this.title;
  },
};
</script>
