<template>
  <div class="active-block">
    <p class="mini-title">Активация блока</p>
    <div class="active-block-input">
      <div
        class="active-block-error__fail"
        v-if="this.validationErrors === 'FAIL'"
      >
        <p class="active-block-error__error-input">
          Извините, попробуйте позже!
        </p>
      </div>
      <label for="active">Введите серийный номер блока:</label>
      <div class="active-block-error" v-if="this.statusActivation">
        <div
          class="active-block-error__fail"
          v-if="this.statusActivation.data.status === 'FAIL'"
        >
          <p class="active-block-error__error-input">
            {{ this.statusActivation.data.message }}
          </p>
        </div>
      </div>

      <input
        maxlength="14"
        placeholder="xxxxxxxxxxxxxx"
        v-model.trim="$v.serial.$model"
        class="seria-input serial"
      />
      <div class="loader" v-if="isLoading">
        <p class="loader-title">Loading...</p>
        <div class="wrapper">
          <div class="clock"></div>
          <div class="clock"></div>
        </div>
      </div>
      <div class="registr-block__button">
        <button
          type="button"
          class="button btn-primary"
          :disabled="isLoading === true"
          @click="active()"
        >
          Активировать
        </button>
      </div>
      <div class="registr-block__button">
        <button type="button" @click="seri()">Моя</button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  required,
  maxLength,
  minLength,
  email,
  numeric,
  helpers,
} from 'vuelidate/lib/validators';
import { actionTypes } from '@/store/modules/register';
import { mapState } from 'vuex';
import router from '@/router';
import axios from '@/api/axios';
export default {
  name: 'AppActiveBlock',
  data() {
    return {
      activeBlock: {},
      serial: '',
      passport_id: '',
    };
  },
  validations: {
    serial: {
      minLength: minLength(14),
      maxLength: maxLength(14),
      required,
    },
  },

  computed: {
    ...mapState({
      allClients: (state) => state.clients.allClients,
      getWeaponItem: (state) => state.register.getWeaponItem,
      getStorageItem: (state) => state.register.getStorageItem,
      isLoading: (state) => state.register.isLoading,
      statusActivation: (state) => state.register.statusActivation,
      validationErrors: (state) => state.register.validationErrors,
    }),
  },
  methods: {
    active() {
      this.activeBlock = {
        passportSerial: this.allClients.passportData.passportSerial,
        passportNumber: this.allClients.passportData.passportNumber,
        serial: this.serial,
        weaponId: this.getWeaponItem.id,
        storageData: this.getStorageItem,
      };
      this.$store
        .dispatch(actionTypes.getSerial, this.activeBlock)
        .then((response) => {
          if (response.status === 'SUCCESS') {
            router.push({ name: 'success-add' });
          }
        })
        .catch((er) => {
          console.log('Ошибка', er);
        });
    },
    seri() {
      axios
        .put(`http://90.189.217.244:9016/api/1/unit/activate`, null, {
          params: { serial: this.serial },
        })
        .then(function (response) {
          // router.push({ name: 'success-add' });
          console.log(response);
          console.log('SUCCESS!!');
        })
        .catch(function (er) {
          console.log(er, 'FAILURE!!');
        });
    },
    success() {},
  },
};
</script>

