<template>
  <div class="clients">
    <div class="holder" v-if="isLoading">
      <div class="preloader">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="container">
      <div class="clients-wrap">
        <div class="clients__select">
          <select id="standard-select" class="text-input" v-model="select">
            <option value="serial">Поиск по серийному номеру:</option>
            <option value="phone">Поиск по номеру телефона:</option>
            <!-- <option value="name">Поиск по Фамилии:</option> -->
          </select>
        </div>
        <div class="clients__search-container">
          <div
            class="search-container2"
            :class="{ activeBlock: this.select === 'serial' }"
          >
            <div class="clients__search">
              <input
                type="search"
                id="search-bar"
                name="search"
                maxlength="14"
                placeholder="Введите серийный номер блока"
                v-model="search"
                aria-label="Вводите"
                class="input-search"
              />
              <!-- <button @click="onChange(search)"> -->
              <img
                src="../assets/img/search-icon.png"
                class="search-icon"
                alt="Search"
                @click="onChange(search)"
              />
            </div>
          </div>
          <div
            class="search-container2"
            :class="{ activeBlock: this.select === 'phone' }"
          >
            <div class="clients__search">
              <input
                type="tel"
                id="search-bar"
                name="phone"
                class="text-input"
                placeholder="8(___)___-____"
                v-model="phone"
                v-phone
                maxlength="14"
                required
              />
              <img
                src="../assets/img/search-icon.png"
                class="search-icon"
                alt="Search"
                @click="searchPhone(phone)"
              />
            </div>
            <!-- </button> -->
          </div>
        </div>
      </div>
      <div class="clients__row">
        <div class="clients__error" v-if="this.validationErrors">
          <div
            class="mini-title"
            v-if="this.validationErrors.status === 'FAIL'"
          >
            {{ this.validationErrors.message }}
          </div>
        </div>
        <div class="confirm" v-if="!this.allClients && this.confirm">
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="user-times"
            class="svg-inline--fa fa-user-times fa-w-20"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 640 512"
          >
            <path
              fill="currentColor"
              d="M589.6 240l45.6-45.6c6.3-6.3 6.3-16.5 0-22.8l-22.8-22.8c-6.3-6.3-16.5-6.3-22.8 0L544 194.4l-45.6-45.6c-6.3-6.3-16.5-6.3-22.8 0l-22.8 22.8c-6.3 6.3-6.3 16.5 0 22.8l45.6 45.6-45.6 45.6c-6.3 6.3-6.3 16.5 0 22.8l22.8 22.8c6.3 6.3 16.5 6.3 22.8 0l45.6-45.6 45.6 45.6c6.3 6.3 16.5 6.3 22.8 0l22.8-22.8c6.3-6.3 6.3-16.5 0-22.8L589.6 240zM224 256c70.7 0 128-57.3 128-128S294.7 0 224 0 96 57.3 96 128s57.3 128 128 128zm89.6 32h-16.7c-22.2 10.2-46.9 16-72.9 16s-50.6-5.8-72.9-16h-16.7C60.2 288 0 348.2 0 422.4V464c0 26.5 21.5 48 48 48h352c26.5 0 48-21.5 48-48v-41.6c0-74.2-60.2-134.4-134.4-134.4z"
            ></path>
          </svg>
          <p class="confirm__title">{{ this.confirm }}</p>
        </div>
        <div class="clients__column" v-if="this.allClients">
          <div class="clients__first">
            <ItemClients v-bind:client_item="this.allClients" />
          </div>
          <div class="clients__second">
            <PutItemClient
              v-bind:client_item="this.allClients"
              @changeData="changeData"
            />
          </div>
        </div>
      </div>
      <div class="" v-if="this.allClients">
        <p class="mini-title">Блоки Оружий</p>

        <BlocksWeapon
          v-for="weapon in this.allClients.unitInfo"
          :key="weapon.id"
          v-bind:weapon_item="weapon"
          @deleteBlock="deleteBlock"
        />
        <div class="add-blocks-weapon">
          <button type="button" class="button btn-neutral" @click="addToBlock">
            <svg
              aria-hidden="true"
              focusable="false"
              data-prefix="fas"
              data-icon="plus"
              class="svg-inline--fa fa-plus fa-w-14"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 448 512"
            >
              <path
                fill="currentColor"
                d="M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z"
              ></path>
            </svg>
            Добавить новое оружия
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ItemClients from '@/components/ItemClients.vue';
import PutItemClient from '@/components/PutItemClient.vue';
import BlocksWeapon from '@/components/BlocksWeapon.vue';
import { actionTypes } from '@/store/modules/clients';
import { mapState } from 'vuex';
export default {
  name: 'AppClient',
  data() {
    return {
      search: '',
      phone: '',
      select: 'phone',
    };
  },
  components: {
    ItemClients,
    PutItemClient,
    BlocksWeapon,
  },

  computed: {
    ...mapState({
      allClients: (state) => state.clients.allClients,
      isLoading: (state) => state.clients.isLoading,
      validationErrors: (state) => state.clients.validationErrors,
      confirm: (state) => state.clients.confirm,
      statusActivation: (state) => state.register.statusActivation,
    }),
  },

  methods: {
    onChange() {
      this.$store
        .dispatch(actionTypes.getSearchClient, this.search)
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });
    },
    searchPhone() {
      this.$store
        .dispatch(actionTypes.getSearchPhoneClient, this.phone)
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });
    },
    addToBlock() {
      this.$router.push({ name: 'addBlock' });
    },
    deleteBlock(serial) {
      let deleteWeapon = {
        userId: this.allClients.id,
        serial: serial,
      };

      this.$store
        .dispatch(actionTypes.deleteWeaponClient, deleteWeapon)
        .then(() => {
          this.$store.dispatch(
            actionTypes.getSearchPhoneClient,
            this.allClients.contactPhone
          );
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeData() {
      this.$store
        .dispatch(
          actionTypes.getSearchPhoneClient,
          this.allClients.contactPhone
        )
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    if (this.allClients) {
      this.changeData();
    }
  },
};
</script>

