<template>
  <div>
    <p class="error-input" v-if="isActiveError">Адрес введен не полностью!</p>
    <div class="data-title" v-if="this.getStorageItem">
      <p>{{ this.getStorageItem.storage }}</p>
    </div>
    <div class="" v-if="!this.getStorageItem">
      <vue-dadata
        v-if="isChecked === false"
        token="ebd2ba512adfc5a0a92d682a21f03adda0331b04"
        :onChange="onChange"
        :autoload="true"
      />
      <div class="checkbox-block">
        <label class="switch" for="checkbox">
          <input type="checkbox" id="checkbox" v-model="isChecked" />
          <div class="slider round"></div>
        </label>
        <p>Не можете найти свой адрес?</p>
      </div>
      <div class="checkbox-block-input" v-if="isChecked === true">
        <div class="label-error">
          <label for="registrationAddress">Введите точный адрес:</label>
        </div>
        <input v-model.trim="storageNew" class="text-input" />
      </div>
      <input
        type="button"
        value="Далее"
        class="button btn-primary"
        @click="Submit()"
      />
    </div>
    <input
      v-if="this.getStorageItem"
      type="button"
      value="Далее"
      class="button btn-primary"
      @click="SubmitOk()"
    />
  </div>
</template>

<script>
import VueDadata from 'vue-dadata';
import { actionTypes } from '@/store/modules/register';
import { mapState } from 'vuex';

export default {
  name: 'VueDadata',
  data() {
    return {
      storageData: null,
      isActiveError: false,
      isActiveBlock: false,
      isChecked: false,
      storageNew: '',
    };
  },
  computed: {
    ...mapState({
      getStorageItem: (state) => state.register.getStorageItem,
    }),
  },
  components: {
    'vue-dadata': VueDadata,
  },
  methods: {
    onChange(i) {
      if (i.data.house != null && i.data.street != null) {
        this.isActiveError = false;
        this.storageData = {
          storage: i.value,
          storageLat: i.data.geo_lat,
          storageLon: i.data.geo_lon,
        };
      }
    },
    Submit() {
      if (this.storageData) {
        this.$store
          .dispatch(actionTypes.getStorageItem, this.storageData)
          .then(() => {
            this.isActiveError = false;
            this.$emit('changeSecondBlock');
            this.$emit('close');
          })
          .catch((er) => {
            console.log('Ошибка', er);
          });
      } else if (this.isChecked === true && !this.storageData) {
        this.storageData = {
          storage: this.storageNew,
        };
        this.$store
          .dispatch(actionTypes.getStorageItem, this.storageData)
          .then(() => {
            this.isActiveError = false;
            this.$emit('changeSecondBlock');
            this.$emit('close');
          })
          .catch((er) => {
            console.log('Ошибка', er);
          });
      } else {
        this.isActiveError = true;
      }
    },
    SubmitOk() {
      this.$emit('changeSecondBlock');
    },
  },
};
</script>
