<template>
  <div class="registr">
    <div class="container">
      <div class="registr_title">
        <h1>Добавление нового блока</h1>
      </div>
      <div class="">
        <form class="registr-form">
          <div class="registr-form__row">
            <ul id="progressbar-block">
              <li
                class="active"
                id="choice-weapons"
                @click="switchTabsClass('block')"
              >
                Ввод данных об оружии
              </li>
              <li id="active-block" @click="switchTabsClass('post')">
                Активация блока
              </li>
            </ul>
            <div class="registr-form__column">
              <div
                class="registr-form__wrap"
                data-tab="block"
                id="titletwo"
                v-if="isPostActive"
              >
                <ChoiceOfWeapon @changeSecondBlock="changeSecondBlock" />
              </div>
              <!-- data-tab="post" -->
              <div class="order-wrap2" data-tab="pay" id="titlethee">
                <div class="order-pay" v-if="isPayActive">
                  <NewActiveBlock />
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import ChoiceOfWeapon from '@/components/ChoiceOfWeapon.vue';
import NewActiveBlock from '@/components/NewActiveBlock.vue';

import axios from 'axios';
import { mapState } from 'vuex';
export default {
  data() {
    return {
      title: 'Регистация блока',
      isPostActive: true,
      isPayActive: false,
      isButtonDisabled: false,
    };
  },
  components: {
    ChoiceOfWeapon,
    NewActiveBlock,
  },

  computed: {
    ...mapState({
      isAuthenticated: (state) => state.auth.isAuthenticated,
      token: (state) => state.auth.token,
      allClients: (state) => state.clients.allClients,
      getWeaponItem: (state) => state.register.getWeaponItem,
    }),
  },

  methods: {
    //смена первого блока
    changeFirstBlock() {
      this.switchTabsClass('block');
    },
    //смена второго блока
    changeSecondBlock() {
      this.switchTabsClass('post');
    },
    switchTabsClass(tab) {
      if (tab === 'block') {
        if (this.allClients != null) {
          this.isBlockActive = false;
          this.isPostActive = true;
          this.isPayActive = false;
          let elem = document.getElementById('choice-weapons');
          elem.classList.add('active');
        }
      } else if (tab === 'post') {
        if (this.getWeaponItem.length != 0) {
          this.isBlockActive = false;
          this.isPostActive = false;
          this.isPayActive = true;
          let elem = document.getElementById('active-block');
          elem.classList.add('active');
        }
      } else {
        this.isBlockActive = true;
        this.isPostActive = false;
        this.isPayActive = false;
      }
    },
  },

  beforeMount() {
    document.title = this.title;
  },
  beforeUpdate() {
    document.title = this.title;
  },
};
</script>
