<script>
import {
  required,
  maxLength,
  minLength,
  email,
  numeric,
  helpers,
} from 'vuelidate/lib/validators';
import axios from 'axios';

import PutContact from '@/components/PutContact.vue';
import { actionTypes } from '@/store/modules/register';
import { mapState } from 'vuex';
//Проверка на  18 лет
const addOne = (value) =>
  new Date().getMonth() - new Date(value).getMonth() >= 0 &&
  new Date().getDate() - new Date(value).getDate() >= 0
    ? 1
    : 0;

const diff = (value) =>
  new Date().getFullYear() - new Date(value).getFullYear();

const minValue = (value) => diff(value) - 1 + (addOne(value) ? 1 : 0) >= 18;
export default {
  name: 'AppContactInfo',
  data() {
    return {
      //Контактная информация
      lastName: '',
      firstName: '',
      middleName: '',
      email: '',
      phone: '',
      placeOfResidence: '',
      //Паспортные данные
      passportData: {},
      passportSerial: '',
      passportNumber: '',
      registrationAddress: '',
      passportPlaceOfIssue: '',
      passportCode: '',
      birthDate: '',
      passportDateOfIssue: '',
      items: {},
      confidantData: null,
      submitStatus: null,
      //Доверительное лицо
      confidantFirstName: '',
      confidantLastName: '',
      confidantMiddleName: '',
      confidantPhone: '',
      relationship: '',

      //Добавление фотографии
      file: [],
      newProduct: [],
      addfoto: false,

      isActiveBlock: false,
    };
  },
  validations: {
    lastName: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },

    firstName: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },
    middleName: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },
    email: {
      required,
      email,
    },
    phone: {
      minLength: minLength(14),
      required,
    },
    passportSerial: {
      minLength: minLength(4),
      required,
      numeric,
    },
    passportNumber: {
      minLength: minLength(6),
      required,
      numeric,
    },
    registrationAddress: {
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё0-9-\s]+$/),
      // ^[а-яА-ЯёЁa-zA-Z0-9]+$
      minLength: minLength(2),
      maxLength: maxLength(150),
      required,
    },
    placeOfResidence: {
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё0-9\s]+$/),
      minLength: minLength(2),
      maxLength: maxLength(150),
      required,
    },
    passportPlaceOfIssue: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё\s]+$/),

      minLength: minLength(2),
      maxLength: maxLength(150),
    },

    passportCode: {
      minLength: minLength(6),

      required,
      numeric,
    },

    birthDate: {
      required,
      maxValue: (value) => value < new Date().toISOString(),
      minValue,

      alpha: helpers.regex('alpha', /^[0-9]{4}\-[0-9]{2}\-[0-9]{2}$/),
    },
    passportDateOfIssue: {
      required,
      maxValue: (value) => value < new Date().toISOString(),
      alpha: helpers.regex('alpha', /^[0-9]{4}\-[0-9]{2}\-[0-9]{2}$/),
    },
    confidantFirstName: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },
    confidantLastName: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },
    confidantMiddleName: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },
    confidantPhone: {
      minLength: minLength(14),
      required,
    },
    relationship: {
      required,
      alpha: helpers.regex('alpha', /^[А-Яа-яЁё]*$/),
    },
  },

  components: {
    PutContact,
  },
  computed: {
    ...mapState({
      currentUser: (state) => state.register.currentUser,
      contact: (state) => state.register.contact,
    }),
    birthDateGo: function () {
      let a = this.birthDate;
      const d = new Date(a);
      return d;
    },
    now: function () {
      let fullYear = new Date();
      return fullYear;
    },
    fullYear: function () {
      let readyDate = this.now - this.birthDateGo;

      const addOne =
        this.now.getMonth() - this.birthDateGo.getMonth() >= 0 &&
        this.now.getDate() - this.birthDateGo.getDate() >= 0;
      const diff = this.now.getFullYear() - this.birthDateGo.getFullYear();
      return diff - 1 + (addOne ? 1 : 0);
    },
  },
  methods: {
    submit() {
      if (
        this.confidantFirstName.length ||
        this.confidantLastName.length ||
        this.confidantMiddleName.length ||
        this.confidantPhone.length ||
        this.relationship.length
      ) {
        this.$v.$touch();
        if (this.$v.$invalid) {
          this.submitStatus = 'ERROR in first';
        } else {
          this.request();
        }
      } else {
        this.$v.lastName.$touch();
        this.$v.firstName.$touch();
        this.$v.middleName.$touch();
        this.$v.email.$touch();
        this.$v.phone.$touch();
        this.$v.placeOfResidence.$touch();
        //Паспортные данные
        this.$v.passportSerial.$touch();
        this.$v.passportNumber.$touch();
        this.$v.registrationAddress.$touch();
        this.$v.passportPlaceOfIssue.$touch();
        this.$v.passportCode.$touch();
        this.$v.birthDate.$touch();
        this.$v.passportDateOfIssue.$touch();
        if (
          this.$v.lastName.$invalid ||
          this.$v.firstName.$invalid ||
          this.$v.middleName.$invalid ||
          this.$v.email.$invalid ||
          this.$v.phone.$invalid ||
          this.$v.placeOfResidence.$invalid ||
          this.$v.passportSerial.$invalid ||
          this.$v.passportNumber.$invalid ||
          this.$v.passportPlaceOfIssue.$invalid ||
          this.$v.passportCode.$invalid ||
          this.$v.birthDate.$invalid ||
          this.$v.passportDateOfIssue.$invalid
        ) {
          this.submitStatus = 'ERROR second';
        } else {
          this.request();
        }
      }
    },
    ChangeConfidant(value) {
      this.confidantData = value;
    },
    handleFileUpload(event) {
      console.log('еще не считал');
      this.file = this.$refs.file.files;
      console.log('Зашел');
      [...this.file].forEach((child) => {
        if (!child.type.match('image')) {
          return console.log('Не изображение!');
        }
        const reader = new FileReader(); //создали ридер
        reader.onload = (ev) => {
          this.addfoto = true;
          this.newProduct.push({
            src: ev.target.result,
            file: file,
          });
        };
        reader.readAsDataURL(child); //считывание файла(ассинхронное)
      });
    },
    bytesToSize(bytes) {
      const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
      if (!bytes) {
        return '0 Byte';
      }
      const i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
      return Math.round(bytes / Math.pow(1024, i)) + ' ' + sizes[i];
    },
    removeHandler(index, name) {
      this.newProduct.splice(index, 1);
      this.$refs.file.value = null; // отчищаем значение для возможной загрузки повторного изображения
      this.addfoto = !this.addfoto;
    },
    request() {
      this.$store
        .dispatch(actionTypes.contact, this.item)
        .then(() => {
          if (this.contact.data.status === 'SUCCESS') {
            this.$store
              .dispatch(actionTypes.getDataContact, this.item)
              .then(() => {})
              .catch((er) => {
                console.log(er, 'Error');
              });

            let formData = new FormData();
            let file = this.file;
            formData.append('file', file[0]);
            formData.append('id', this.contact.data.result.userId);
            this.$store
              .dispatch(actionTypes.addPhoto, formData)
              .then(() => {
                this.$emit('validFirstBlock');
              })
              .catch((er) => {
                console.log(er, 'Error');
              });
          }
        })
        .catch((er) => {
          console.log('Ошибка', er);
        });
    },
    changeFirstBlock() {
      this.$emit('validFirstBlock');
    },

    validFirstBlock() {
      //Обработка паспортных данных
      this.passportData = {
        registrationAddress: this.registrationAddress,
        passportPlaceOfIssue: this.passportPlaceOfIssue,
        departmentCode: this.passportCode,
        passportDateOfIssue: this.passportDateOfIssue,
        passportSerial: this.passportSerial,
        passportNumber: this.passportNumber,
        birthDate: this.birthDate,
      };
      //Обработка доверенного лица
      if (
        this.confidantFirstName.length &&
        this.confidantLastName.length &&
        this.confidantMiddleName.length &&
        this.confidantPhone.length &&
        this.relationship.length
      ) {
        this.confidantData = {
          confidantFirstName: this.confidantFirstName,
          confidantLastName: this.confidantLastName,
          confidantMiddleName: this.confidantMiddleName,
          confidantPhone: this.confidantPhone,
          relationship: this.relationship,
        };
      } else this.confidantData = null;

      //Объект для отправки
      this.item = {
        lastName: this.lastName,
        firstName: this.firstName,
        middleName: this.middleName,
        email: this.email,
        contactPhone: this.phone,
        placeOfResidence: this.placeOfResidence,
        passportData: this.passportData,
        confidantData: this.confidantData,
      };

      this.submit();
    },
  },
};
</script>

