import registerAPi from '@/api/register.js';
import getWeaponAPi from '@/api/weapon.js';
import getSerialAPi from '@/api/activation.js';
import { setItem, getItem } from '@/helpers/persistanceStorage';

const state = {
  isSubmitting: false,
  isLoading: false, //ожидание запроса
  isAuthenticated: false, //авторизирован ли пользователь
  validationErrors: null, //ошибки
  allWeapon: null,
  weapon: null,
  // getWeaponItem: [],
  getWeaponItem: getItem('currentWepon' || null),
  // getWeaponItem: JSON.parse(localStorage.getItem("currentWepon") || []),
  contact: JSON.parse(localStorage.getItem('contact') || null), //id и ошибка
  // currentUser: [],
  currentUser: getItem('currentUser' || null),
  // currentUser: JSON.parse(localStorage.getItem("currentUser") || []),
  getStorageItem: getItem('currentStorage' || null),
  statusActivation: JSON.parse(
    localStorage.getItem('statusActivation') || null
  ),
};

export const mutationTypes = {
  contactStart: '[register] contactStart',
  contactSuccess: '[register] contactSuccess',
  contactFailure: '[register] contactFailure',

  addPhotoStart: '[register] addPhotoStart',
  addPhotoSuccess: '[register] addPhotoSuccess',
  addPhotoFailure: '[register] addPhotoFailure',

  PutContactStart: '[register] PutContactStart',
  PutContactSuccess: '[register] PutContactSuccess',
  PutContactFailure: '[register] PutContactFailure',

  PutContactItemStart: '[register]   PutContactItemStart',
  PutContactItemSuccess: '[register]   PutContactItemSuccess',
  PutContactItemFailure: '[register]   PutContactItemFailure',

  getAllWeaponStart: '[register] getAllWeaponStart',
  getAllWeaponSuccess: '[register] getAllWeaponSuccess',
  getAllWeaponFailure: '[register] getAllWeaponFailure',

  getWeaponStart: '[register] getWeaponStart',
  getWeaponSuccess: '[register] getWeaponSuccess',
  getWeaponFailure: '[register] getWeaponFailure',
  getDataContactSuccess: '[register] getDataContactSuccess',

  getWeaponItemSuccess: '[register] getWeaponItemSuccess',

  changeWeaponItemSuccess: '[register] changeWeaponItemSuccess',
  getStorageItemSuccess: '[register] getStorageItemSuccess',
  getSerialStart: '[register] getSerialStart',
  getSerialSuccess: '[register] getSerialSuccess',
  getSerialFailure: '[register] getSerialFailure',
};
export const actionTypes = {
  contact: '[register] contact',
  addPhoto: '[register] addPhoto',
  PutContact: '[register] putContact',
  PutContactItem: '[register] putContactItem',
  getAllWeapon: '[register] getAllWeapon',
  getWeapon: '[register] getWeapon',
  getWeaponItem: '[register] getWeaponItem',
  changeWeaponItem: '[register] changeWeaponItem',
  getStorageItem: '[register] getStorageItem',
  getDataContact: '[register] getDataContact',
  getSerial: '[register] getSerial',
};

const mutations = {
  [mutationTypes.contactStart](state) {
    state.isLoading = true;
    state.isAuthenticated = false;
    state.validationErrors = null;
  },
  [mutationTypes.contactSuccess](state, payload) {
    state.isAuthenticated = true;
    state.isLoading = false;
    state.contact = payload;
    state.isLoggedIn = true;
  },
  [mutationTypes.contactFailure](state, payload) {
    state.isAuthenticated = false;
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.addPhotoStart](state) {
    state.isLoading = true;
    state.isAuthenticated = false;
    state.validationErrors = null;
  },
  [mutationTypes.addPhotoSuccess](state) {
    state.isLoading = false;
    state.isAuthenticated = true;
  },
  [mutationTypes.addPhotoFailure](state, payload) {
    state.isLoading = false;
    state.isAuthenticated = false;
    state.validationErrors = payload;
  },

  [mutationTypes.getAllWeaponStart](state) {
    state.isLoading = true;

    state.validationErrors = null;
  },
  [mutationTypes.getAllWeaponSuccess](state, payload) {
    state.isLoading = false;
    state.allWeapon = payload;
  },
  [mutationTypes.getAllWeaponFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.getWeaponStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.getWeaponSuccess](state, payload) {
    state.isLoading = false;
    state.weapon = payload;
  },
  [mutationTypes.getWeaponFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.getDataContactSuccess](state, payload) {
    state.isLoading = false;
    state.currentUser = payload;

    localStorage.setItem('currentUser', JSON.stringify(state.currentUser));
  },
  [mutationTypes.getWeaponItemSuccess](state, payload) {
    state.isLoading = false;
    state.getWeaponItem = payload;
    localStorage.setItem('currentWepon', JSON.stringify(state.getWeaponItem));
  },
  [mutationTypes.changeWeaponItemSuccess](state) {
    state.isLoading = false;
    state.getWeaponItem = null;
    localStorage.removeItem('currentWepon');
  },

  [mutationTypes.getStorageItemSuccess](state, payload) {
    state.isLoading = false;
    state.getStorageItem = payload;
    localStorage.setItem(
      'currentStorage',
      JSON.stringify(state.getStorageItem)
    );
  },

  [mutationTypes.getSerialStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
    state.statusActivation = null;
  },
  [mutationTypes.getSerialSuccess](state, payload) {
    state.statusActivation = payload;
    state.isLoading = false;
  },
  [mutationTypes.getSerialFailure](state) {
    state.isLoading = false;
    state.validationErrors = 'FAIL';
  },

  [mutationTypes.PutContactStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.PutContactSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.PutContactFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.PutContactItemStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.PutContactItemSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.PutContactItemFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  //Регистрация пользователя
  [actionTypes.contact](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.contactStart);

      registerAPi
        .contact(credentials)
        .then((response) => {
          context.commit(mutationTypes.contactSuccess, response);
          window.localStorage.setItem('contact', response);
          setItem('contact', response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.contactFailure, resp);
          console.log('ошибка', resp);
        });
    });
  },
  [actionTypes.addPhoto](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.addPhotoStart);

      registerAPi
        .addPhoto(credentials)
        .then((response) => {
          context.commit(mutationTypes.addPhotoSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.addPhotoFailure, resp);
          console.log('ошибка', resp);
        });
    });
  },
  //Изменение данных о пользователе
  [actionTypes.PutContact](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.PutContactStart);
      registerAPi
        .PutContact(credentials)
        .then((response) => {
          localStorage.setItem('currentUser', JSON.stringify(credentials));
          state.currentUser = credentials;
          context.commit(mutationTypes.PutContactSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.PutContactFailure, resp);
          console.log('ошибка', resp);
        });
    });
  },
  [actionTypes.PutContactItem](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.PutContactItemStart);
      registerAPi
        .PutContact(credentials)
        .then((response) => {
          localStorage.setItem('allClients', JSON.stringify(credentials));
          context.commit(mutationTypes.PutContactItemSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.PutContactItemFailure, resp);
          console.log('ошибка', resp);
        });
    });
  },
  //получение данных о зарегестрированном клиенте
  [actionTypes.getDataContact](context, credentials) {
    return context.commit(mutationTypes.getDataContactSuccess, credentials);
  },
  //выбранное оружие
  [actionTypes.getWeaponItem](context, credentials) {
    return context.commit(mutationTypes.getWeaponItemSuccess, credentials);
  },
  [actionTypes.changeWeaponItem](context) {
    return context.commit(mutationTypes.changeWeaponItemSuccess);
  },
  //выбранные адрес хранения оружия
  [actionTypes.getStorageItem](context, credentials) {
    return context.commit(mutationTypes.getStorageItemSuccess, credentials);
  },

  [actionTypes.getAllWeapon](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getAllWeaponStart);
      getWeaponAPi
        .getAllWeapon()
        .then((response) => {
          context.commit(mutationTypes.getAllWeaponSuccess, response.data);
          resolve(response.data);
        })
        .catch((err) => {
          context.commit(
            mutationTypes.getAllWeaponFailure,
            // result.response.data,
            console.log('error', err)
          );
        });
    });
  },
  [actionTypes.getWeapon](context, path) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getWeaponStart);
      getWeaponAPi
        .getWeapon(path)
        .then((response) => {
          context.commit(mutationTypes.getWeaponSuccess, response.data.result);
          resolve(response.data.result);
        })
        .catch((err) => {
          context.commit(
            mutationTypes.getWeaponFailure,
            console.log('error', err)
          );
        });
    });
  },
  //Серийник
  [actionTypes.getSerial](context, credentials) {
    return new Promise((resolve) => {
      localStorage.removeItem('statusActivation'); //отчистка выбранного оружия
      state.statusActivation === null;
      context.commit(mutationTypes.getSerialStart);
      getSerialAPi
        .getSerial(credentials)
        .then((response) => {
          context.commit(mutationTypes.getSerialSuccess, response);
          if (response.data.status === 'SUCCESS') {
            window.localStorage.setItem('statusActivation', response);
            setItem('statusActivation', response);
            // localStorage.removeItem("allClients"); //отчистка пользователя при добавлении нового блока
            // state.allClients = null;
            localStorage.removeItem('currentStorage'); //отчистка координат
            state.getStorageItem = null;
            localStorage.removeItem('currentUser'); //отчистка пользователя
            state.currentUser = null;
            localStorage.removeItem('currentWepon'); //отчистка выбранного оружия
            state.getWeaponItem = null;
          }
          resolve(response.data);
        })
        .catch((err) => {
          context.commit(
            mutationTypes.getSerialFailure,
            console.log('error', err)
          );
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
