<template>
  <div class="client-block">
    <h3 class="client-block__name">
      {{ this.client_item.lastName }}
      {{ this.client_item.firstName }}
      {{ this.client_item.middleName }}
    </h3>
    <div class="client-block__img" v-if="isAddfoto === false">
      <img :src="`${this.client_item.downloadUrl}`" alt="" />
    </div>
    <div v-if="isAddfoto === true && this.newPhoto.length">
      <div class="client-block__img" id="foto">
        <div
          class="photo-item__column"
          v-for="(file, index) in this.newPhoto"
          :key="index"
        >
          <div class="preview-image">
            <div
              class="preview-remove"
              :data-name="file.file.name"
              @click="removeHandler(index, file.file.name)"
            >
              X
            </div>
            <div class="preview-block">
              <img :src="file.src" alt="foto" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="example-2" v-if="isAddfoto === false">
      <div class="form-group">
        <form ref="myForm">
          <input
            type="file"
            id="file"
            ref="file"
            accept="image/jpeg"
            @change="handleFileUpload"
            class="input-file"
          />
          <label for="file" class="btn btn-tertiary-new js-labelFileNew">
            <svg
              aria-hidden="true"
              focusable="false"
              data-prefix="fas"
              data-icon="download"
              class="svg-inline--fa fa-download fa-w-16"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 512 512"
            >
              <path
                fill="currentColor"
                d="M216 0h80c13.3 0 24 10.7 24 24v168h87.7c17.8 0 26.7 21.5 14.1 34.1L269.7 378.3c-7.5 7.5-19.8 7.5-27.3 0L90.1 226.1c-12.6-12.6-3.7-34.1 14.1-34.1H192V24c0-13.3 10.7-24 24-24zm296 376v112c0 13.3-10.7 24-24 24H24c-13.3 0-24-10.7-24-24V376c0-13.3 10.7-24 24-24h146.7l49 49c20.1 20.1 52.5 20.1 72.6 0l49-49H488c13.3 0 24 10.7 24 24zm-124 88c0-11-9-20-20-20s-20 9-20 20 9 20 20 20 20-9 20-20zm64 0c0-11-9-20-20-20s-20 9-20 20 9 20 20 20 20-9 20-20z"
              ></path>
            </svg>
            <span class="js-fileName">Изменить фотографию</span>
          </label>
        </form>
      </div>
    </div>
    <button
      type="submit"
      class="btn btn-white"
      v-if="isAddfoto === true && this.newPhoto.length"
      @click="putImage()"
    >
      Загрузить
    </button>
    <div class="client-block__info">
      <p class="clients__info-title">
        Email:
        <span>{{ this.client_item.email }}</span>
      </p>
      <p class="clients__info-title">
        Телефон:
        <span>{{ this.client_item.contactPhone }}</span>
      </p>
    </div>
    <div class="client-block__info-btn">
      <div class="client-block__info-btn-item" @click="show()">
        <span class="link-text">
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="arrow-left"
            class="svg-inline--fa fa-arrow-left fa-w-14 svg1"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 448 512"
          >
            <path
              fill="#21399b"
              d="M257.5 445.1l-22.2 22.2c-9.4 9.4-24.6 9.4-33.9 0L7 273c-9.4-9.4-9.4-24.6 0-33.9L201.4 44.7c9.4-9.4 24.6-9.4 33.9 0l22.2 22.2c9.5 9.5 9.3 25-.4 34.3L136.6 216H424c13.3 0 24 10.7 24 24v32c0 13.3-10.7 24-24 24H136.6l120.5 114.8c9.8 9.3 10 24.8.4 34.3z"
            ></path>
          </svg>
          Удалить
        </span>
      </div>
    </div>
    <ModalDelete
      v-if="showModal"
      @close="showModal = false"
      v-bind:client="this.client_item"
    >
    </ModalDelete>
  </div>
</template>

<script>
import ModalDelete from '@/components/ModalDelete.vue';
import { actionTypes } from '@/store/modules/register';
export default {
  name: 'AppItemClient',
  data() {
    return {
      id: '',
      showModal: false,
      newPhoto: [],
      isAddfoto: false,
      file: [],
    };
  },
  props: {
    client_item: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  components: {
    ModalDelete,
  },
  methods: {
    show() {
      this.showModal = true;
    },
    handleFileUpload(event) {
      this.file = this.$refs.file.files;
      [...this.file].forEach((child) => {
        if (!child.type.match('image')) {
          return console.log('Не изображение!');
        }
        const reader = new FileReader(); //создали ридер
        reader.onload = (ev) => {
          this.isAddfoto = true;
          this.newPhoto.push({
            src: ev.target.result,
            file: file,
          });
        };
        reader.readAsDataURL(child); //считывание файла(ассинхронное)
      });
    },
    putImage() {
      let formData = new FormData();
      let file = this.file;
      formData.append('file', file[0]);
      formData.append('id', this.client_item.id);
      this.$store
        .dispatch(actionTypes.addPhoto, formData)
        .then(() => {
          this.isAddfoto = false;
          this.newPhoto = [];
        })
        .catch((er) => {
          console.log(er, 'Error');
        });
    },

    removeHandler(index, name) {
      this.newPhoto.splice(index, 1);
      this.isAddfoto = !this.isAddfoto;
    },
  },
};
</script>

