              @click="submit()"
            />
            <input
              type="button"
              value="НЕТ"
              class="modal__btn"
              @click="$emit('close')"
            />
          </div>
          <div class="modal-footer">
            <slot name="footer"> </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
