<script>
import { actionTypes } from "@/store/modules/register";
export default {
  name: "AppModal",
  props: {
    weapon: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    submit() {
      console.log(this.weapon);
      this.$store
        .dispatch(actionTypes.changeWeaponItem)
        .then(() => {
          // this.$emit("changeSecondBlock");
          this.$emit("close");
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
  },
};
</script>

