    <form @submit.prevent="putContact" class="put-succsess-blok">
      <div class="registr-block__put-first">
        <p class="mini-title">Контактная информация</p>
        <div class="registr-block__input">
          <label for="name">Фамилия</label>
          <input
            type="text"
            id="name"
            class="text-input"
