            v-bind:value="this.client_item.lastName"
            required
          />
          <label for="name">Имя</label>
          <input
            type="text"
            id="name"
            class="text-input"
            name="firstName"
            v-bind:value="this.client_item.firstName"
            required
          />
          <label for="name">Отчество</label>
          <input
            type="text"
            id="name"
            class="text-input"
            name="middleName"
            v-bind:value="this.client_item.middleName"
            required
          />
        </div>
        <div class="registr-block__input">
          <label for="email">Электронная почта</label>
          <input
            type="text"
            id="email"
            name="email"
            v-bind:value="this.client_item.email"
            class="text-input"
            required
          />
        </div>
        <div class="registr-block__input">
          <label for="phone">Телефон</label>
          <input
            type="tel"
            id="phonefooter"
            name="contactPhone"
            class="text-input"
            placeholder="8(___)-___-____"
            v-bind:value="this.client_item.contactPhone"
            v-phone
            required
            maxlength="15"
          />
        </div>
      </div>
      <div class="registr-block__put-pass-data">
        <p class="mini-title">Паспортные данные гражданина РФ</p>
        <div class="registr-block__passport">
          <div class="passport">
            <div class="passport__block margin">
              <label for="passportSerial">Серия:</label>
              <input
                maxlength="4"
                placeholder="xxxx"
                passportData
                name="passportSerial"
                v-bind:value="this.client_item.passportData.passportSerial"
                class="seria-input seria"
              />
            </div>
            <div class="passport__block">
              <label for="passportNumber">Номер:</label>
              <input
                maxlength="6"
