                class="seria-input pass"
              />
            </div>
          </div>
          <div class="passport__block">
            <label for="birthDate">Дата рождения</label>
            <input
              type="date"
              id="start"
              name="birthDate"
              v-bind:value="this.client_item.passportData.birthDate"
              class="text-input-putdata"
              placeholder="дд.мм.гггг"
            />
          </div>
        </div>
        <div class="passport__block">
          <label for="registrationAddress">Адрес Регистрации:</label>
          <input
            name="registrationAddress"
            v-bind:value="this.client_item.passportData.registrationAddress"
            class="text-input"
          />
          <label for="registrationAddress"
            >Адрес Фактического проживания:</label
          >
          <input
            name="placeOfResidence"
            v-bind:value="this.client_item.placeOfResidence"
            class="text-input"
          />
          <label for="passportPlaceOfIssue">Кем выдан документ:</label>
          <input
            name="passportPlaceOfIssue"
            v-bind:value="this.client_item.passportData.passportPlaceOfIssue"
            placeholder="Наименование подразделения"
            class="text-input"
          />
        </div>
        <div class="passport__block2">
          <div class="passport__block-put">
            <label for="departmentCode">Код подразделения:</label>
            <input
              maxlength="6"
              placeholder="xxxxxx"
              name="departmentCode"
              v-bind:value="this.client_item.passportData.departmentCode"
              class="seria-input kod"
            />
          </div>
          <div class="passport__block">
            <label for="passportDateOfIssue">Дата выдачи документа:</label>
            <input
              type="date"
              id="start"
              name="passportDateOfIssue"
              v-bind:value="this.client_item.passportData.passportDateOfIssue"
              class="text-input-putdata"
              placeholder="дд.мм.гггг"
            />
          </div>
        </div>
      </div>
      <div class="register-trustee" v-if="this.client_item.confidantData">
        <div
          class="register-trustee__title"
          @click="isActiveBlock = !isActiveBlock"
        >
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="far"
            data-icon="plus-square"
            class="svg-inline--fa fa-plus-square fa-w-14"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 448 512"
          >
            <path
              fill="black"
              d="M352 240v32c0 6.6-5.4 12-12 12h-88v88c0 6.6-5.4 12-12 12h-32c-6.6 0-12-5.4-12-12v-88h-88c-6.6 0-12-5.4-12-12v-32c0-6.6 5.4-12 12-12h88v-88c0-6.6 5.4-12 12-12h32c6.6 0 12 5.4 12 12v88h88c6.6 0 12 5.4 12 12zm96-160v352c0 26.5-21.5 48-48 48H48c-26.5 0-48-21.5-48-48V80c0-26.5 21.5-48 48-48h352c26.5 0 48 21.5 48 48zm-48 346V86c0-3.3-2.7-6-6-6H54c-3.3 0-6 2.7-6 6v340c0 3.3 2.7 6 6 6h340c3.3 0 6-2.7 6-6z"
            ></path>
          </svg>
          <p class="mini-title">Доверительное лицо</p>
        </div>
        <div class="register-trustee__block" v-if="isActiveBlock === true">
          <div class="registr-block__input-confidant">
            <div class="registr-block__wrap">
              <label for="confidantLastName">Имя</label>
              <input
                type="text"
                id="confidantLastName"
                class="text-input"
                name="confidantLastName"
                v-bind:value="this.client_item.confidantData.lastName"
                required
              />
            </div>
            <div class="registr-block__wrap">
              <label for="confidantFirstName">Фамилия</label>
              <input
                type="text"
                id="confidantFirstName"
                name="confidantFirstName"
                class="text-input"
                v-bind:value="this.client_item.confidantData.firstName"
                required
              />
            </div>
            <div class="registr-block__wrap">
              <label for="confidantMiddleName">Отчество</label>
              <input
                type="text"
                id="confidantMiddleName"
                name="confidantMiddleName"
                class="text-input"
                v-bind:value="this.client_item.confidantData.middleName"
                required
              />
            </div>
          </div>
          <div class="registr-block__input-confidant">
            <div class="registr-block__wrap">
              <label for="confidantPhone">Телефон:</label>
              <input
                type="tel"
                id="phonefooter"
                name="confidantPhone"
                class="text-input"
                placeholder="8(___)___-____"
                v-bind:value="this.client_item.confidantData.phone"
                v-phone
                required
                maxlength="14"
              />
            </div>
            <div class="registr-block__wrap">
              <div class="label-error-confidant">
                <label for="relationship">Кем приходится:</label>
              </div>
              <div class="select">
                <select
                  id="standard-select"
                  class="text-input"
                  name="relationship"
                  v-bind:value="this.client_item.confidantData.relationship"
                  required
                >
                  <option value="Мать">Мать</option>
                  <option value="Отец">Отец</option>
                  <option value="Сестра">Сестра</option>
                  <option value="Брат">Брат</option>
                  <option value="Друг">Друг</option>
                  <option value="Другое">Другое</option>
                </select>
              </div>
            </div>
