      </div>
      <div class="registr-block__button">
        <button type="submit" class="button btn-neutral">
          Редактировать данные
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { actionTypes } from '@/store/modules/register';

export default {
  name: 'AppPutClient',
  data() {
    return {
      putData: [],
      items: {},
      passportData: {},
      isActiveBlock: false,
    };
  },
  props: {
    client_item: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  components: {},
  methods: {
    putContact() {
      const forms = document.forms;
      const formData = new FormData(forms[0]);
      this.putData = [];
      for (let [name, value] of formData) {
        this.putData = { ...this.putData, [name]: value };
      }
      this.validFirstBlock();
    },
    submit() {
      this.$store
        .dispatch(actionTypes.PutContactItem, this.items)
        .then(() => {
          this.$emit('changeData');
        })
        .catch((er) => {
          console.log('Ошибка', er);
        });
    },
    validFirstBlock() {
      //Обработка паспортных данных
      this.passportData = {
        registrationAddress: this.putData.registrationAddress,
        passportPlaceOfIssue: this.putData.passportPlaceOfIssue,
        departmentCode: this.putData.departmentCode,
        passportDateOfIssue: this.putData.passportDateOfIssue,
        passportSerial: this.putData.passportSerial,
        passportNumber: this.putData.passportNumber,
        birthDate: this.putData.birthDate,
      };
      //Обработка доверенного лица
      if (
        this.putData.firstName &&
        this.putData.lastName &&
        this.putData.middleName &&
        this.putData.phone &&
        this.putData.relationship
      ) {
        this.confidantData = {
          confidantFirstName: this.putData.firstName,
          confidantLastName: this.putData.lastName,
          confidantMiddleName: this.putData.middleName,
          confidantPhone: this.putData.phone,
          relationship: this.putData.relationship,
        };
      } else this.confidantData = null;

      //Объект для отправки
      this.items = {
        id: this.client_item.id,
        lastName: this.putData.lastName,
        firstName: this.putData.firstName,
        middleName: this.putData.middleName,
        email: this.putData.email,
        contactPhone: this.putData.contactPhone,
        placeOfResidence: this.putData.placeOfResidence,
        passportData: this.passportData,
        confidantData: this.confidantData,
      };
      this.submit();
    },
  },
};
</script>

